@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
#game_board {
    flex-grow: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    padding: 0.25rem 0.5rem;
}

#game_board #meaning {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#game_board #meaning * {
    margin: 0;
    margin-bottom: 0.5rem;
}

#game_board #meaning #label {
    font-weight: 600;
}

#word > input{
    text-transform: capitalize;
    font-size: 2rem;
    width: 2rem;
    margin-right: 0.25rem;
    border: none;
    caret-color: transparent;
    margin-bottom: 0.25rem;
    background: none;
}

#word > input:focus {
    background-color: var(--focus);
}

#word > input:hover{
    cursor: pointer;
}

#word > input:focus {
    outline: none;
    background-color: rgb(200,200,200);
}

#word .correct{
    border-bottom: 0.25rem solid var(--correct);
}

#word .not_correct{
    border-bottom: 0.25rem solid var(--not-correct);
}

#word .preset{
    border-bottom: 0.25rem solid var(--preset);
}

#word {
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 100%;
}

#won {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 6rem;
}

#won > * {
    padding: 0;
    margin: 0;
    margin-bottom: 1rem;
}

@media (min-width: 768px){
    #game_board #meaning {
        flex-direction: row;
    }

    #game_board #meaning {
        margin-bottom: 0.5rem;
    }

    #game_board #meaning #label {
        margin-right: 0.5rem;
    }

    #game_board #meaning #label::after {
        content: ':';
    }
}

.logo {
    height: 50px;
    width: 50px;
}

.logo-container {
    margin-right: auto;
}

.logo.dark {
    display: none;
}

.dark .logo {
    display: none;
}

.dark .logo.dark {
    display: block;
}

nav {
    padding: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.theme-toggle {
    display: flex;
    flex-direction: row;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

#theme-toggle{
    display: none;
}

#theme-toggle+label, #theme-toggle:checked+label {
    position: relative;
    display: flex;
    height: 1.5rem;
    width: 3rem;
    border-radius: 1.5rem;
    background-color: var(--text);
    border: none;
    transition: all ease 0.5s;
    border: 1px solid var(--text);
    box-sizing: content-box;
}

#theme-toggle+label::after {
    content: '';
    position: absolute;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 1.5rem;
    background-color: var(--background);
    top: 0;
    right: 0;
}

#theme-toggle:checked+label::after {
    left: 0;
}


.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
}

.start {
    width: -webkit-max-content;
    width: max-content;
}


:root {
    --background: #fff;
    --text: #171717;
    --focus: #2221;
    --correct: #5cc45c;
    --not-correct: #852525;
    --preset: #444;
    --buton: #152370;
}

:root.dark {
    --background: #001f1d;
    --text: #fff;
    --preset: #bbb;
}

h1, h2, h3, h4, h5, h6, p, a, input, img {
    color: #171717;
    color: var(--text);
    transition: color linear 0.5s;
}

body {
    background-color: #fff;
    background-color: var(--background);
    margin: 0;
    padding: 0;
    transition: background-color linear 0.5s;
}

* {
    box-sizing: border-box;
    font-size: 1.25rem;
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

#root {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    max-width: 100vw;
    max-height: 100vh;
}

.button {
    background-color: #152370;
    background-color: var(--buton);
    color: #fff;
    padding: 0.2rem 0.5rem;
    height: 2rem;
    border-radius: 1rem;
    box-shadow: none;
    border: none;
    cursor: pointer;
}

.loading {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .loading div {
    display: inline-block;
    position: absolute;
    left: 8px;
    width: 16px;
    background: var(--text);
    -webkit-animation: loading 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
            animation: loading 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
  }
  .loading div:nth-child(1) {
    left: 8px;
    -webkit-animation-delay: -0.24s;
            animation-delay: -0.24s;
  }
  .loading div:nth-child(2) {
    left: 32px;
    -webkit-animation-delay: -0.12s;
            animation-delay: -0.12s;
  }
  .loading div:nth-child(3) {
    left: 56px;
    -webkit-animation-delay: 0;
            animation-delay: 0;
  }
  @-webkit-keyframes loading {
    0% {
      top: 8px;
      height: 64px;
    }
    50%, 100% {
      top: 24px;
      height: 32px;
    }
  }
  @keyframes loading {
    0% {
      top: 8px;
      height: 64px;
    }
    50%, 100% {
      top: 24px;
      height: 32px;
    }
  }
