.logo {
    height: 50px;
    width: 50px;
}

.logo-container {
    margin-right: auto;
}

.logo.dark {
    display: none;
}

.dark .logo {
    display: none;
}

.dark .logo.dark {
    display: block;
}

nav {
    padding: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.theme-toggle {
    display: flex;
    flex-direction: row;
    gap: 0.5rem;
}

#theme-toggle{
    display: none;
}

#theme-toggle+label, #theme-toggle:checked+label {
    position: relative;
    display: flex;
    height: 1.5rem;
    width: 3rem;
    border-radius: 1.5rem;
    background-color: var(--text);
    border: none;
    transition: all ease 0.5s;
    border: 1px solid var(--text);
    box-sizing: content-box;
}

#theme-toggle+label::after {
    content: '';
    position: absolute;
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 1.5rem;
    background-color: var(--background);
    top: 0;
    right: 0;
}

#theme-toggle:checked+label::after {
    left: 0;
}
