@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

:root {
    --background: #fff;
    --text: #171717;
    --focus: #2221;
    --correct: #5cc45c;
    --not-correct: #852525;
    --preset: #444;
    --buton: #152370;
}

:root.dark {
    --background: #001f1d;
    --text: #fff;
    --preset: #bbb;
}

h1, h2, h3, h4, h5, h6, p, a, input, img {
    color: var(--text);
    transition: color linear 0.5s;
}

body {
    background-color: var(--background);
    margin: 0;
    padding: 0;
    transition: background-color linear 0.5s;
}

* {
    box-sizing: border-box;
    font-size: 1.25rem;
    text-align: center;
    font-family: 'Roboto', sans-serif;
}

#root {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    max-width: 100vw;
    max-height: 100vh;
}

.button {
    background-color: var(--buton);
    color: #fff;
    padding: 0.2rem 0.5rem;
    height: 2rem;
    border-radius: 1rem;
    box-shadow: none;
    border: none;
    cursor: pointer;
}
