#game_board {
    flex-grow: 1;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    padding: 0.25rem 0.5rem;
}

#game_board #meaning {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#game_board #meaning * {
    margin: 0;
    margin-bottom: 0.5rem;
}

#game_board #meaning #label {
    font-weight: 600;
}

#word > input{
    text-transform: capitalize;
    font-size: 2rem;
    width: 2rem;
    margin-right: 0.25rem;
    border: none;
    caret-color: transparent;
    margin-bottom: 0.25rem;
    background: none;
}

#word > input:focus {
    background-color: var(--focus);
}

#word > input:hover{
    cursor: pointer;
}

#word > input:focus {
    outline: none;
    background-color: rgb(200,200,200);
}

#word .correct{
    border-bottom: 0.25rem solid var(--correct);
}

#word .not_correct{
    border-bottom: 0.25rem solid var(--not-correct);
}

#word .preset{
    border-bottom: 0.25rem solid var(--preset);
}

#word {
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 100%;
}

#won {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 6rem;
}

#won > * {
    padding: 0;
    margin: 0;
    margin-bottom: 1rem;
}

@media (min-width: 768px){
    #game_board #meaning {
        flex-direction: row;
    }

    #game_board #meaning {
        margin-bottom: 0.5rem;
    }

    #game_board #meaning #label {
        margin-right: 0.5rem;
    }

    #game_board #meaning #label::after {
        content: ':';
    }
}
